	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<p>Attack surfaces that hackers can hack on. There are different <a href=\"/programs/asset-types.html\">types</a> such as: CIDR, Domain, Source code, Executable, Hardware/loT, iOS: .ipa.</p>\n<p>A collection of assets creates a scope.</p>","frontmatter":{"path":"/glossary/asset","title":"Asset"}}},{"node":{"html":"<p>A financial reward offered in exchange for a valid vulnerability report.</p>","frontmatter":{"path":"/glossary/bounty","title":"Bounty"}}},{"node":{"html":"<p>A <a href=\"/programs/bounty-tables.html\">bounty table</a> illustrates how much an organization is willing to pay for various bugs, helps set expectations for hackers, and gives the bug bounty team a guideline to ensure fair and consistent reward amounts.</p>","frontmatter":{"path":"/glossary/bounty-table","title":"Bounty Table"}}},{"node":{"html":"<p>A bug bounty offers monetary incentives for vulnerabilities and invites submissions from hackers.</p>","frontmatter":{"path":"/glossary/bug-bounty-program","title":"Bug Bounty Program"}}},{"node":{"html":"<p>Common Vulnerability Scoring System (<a href=\"/programs/severity.html\">CVSS</a>) is the framework HackerOne utilizes to assign a severity rating to a vulnerability.</p>","frontmatter":{"path":"/glossary/cvss","title":"CVSS"}}},{"node":{"html":"<p>Common Weakness Enumeration (<a href=\"/hackers/weakness.html\">CWE</a>) is the framework HackerOne utilizes to assign a weakness to a vulnerability.</p>","frontmatter":{"path":"/glossary/cwe","title":"CWE"}}},{"node":{"html":"<p>A saved response or template that can be applied repeatedly to reports. </p>","frontmatter":{"path":"/glossary/common-response","title":"Common Response"}}},{"node":{"html":"<p>The HackerOne <a href=\"/hackers/directory.html\">directory</a> is a community-curated resource for contacting an organization regarding a security vulnerability.</p>","frontmatter":{"path":"/glossary/directory","title":"Directory"}}},{"node":{"html":"<p>Someone who’s able to find vulnerabilities in information-related systems.</p>","frontmatter":{"path":"/glossary/hacker","title":"Hacker"}}},{"node":{"html":"<p><a href=\"/hackers/hacktivity.html\">Hacktivity</a> is the public community feed that showcases hacker activity on HackerOne. </p>","frontmatter":{"path":"/glossary/hacktivity","title":"Hacktivity"}}},{"node":{"html":"<p>An international standard describing vulnerability coordination.</p>","frontmatter":{"path":"/glossary/iso-29147","title":"ISO 29147"}}},{"node":{"html":"<p>An international standard describing vulnerability handling processes.</p>","frontmatter":{"path":"/glossary/iso-30111","title":"ISO 30111"}}},{"node":{"html":"<p>Average <a href=\"/hackers/reputation.html\">reputation</a> gained per bounty.</p>","frontmatter":{"path":"/glossary/impact","title":"Impact"}}},{"node":{"html":"<p>External applications being connected and functioning in HackerOne.</p>","frontmatter":{"path":"/glossary/integration","title":"Integration"}}},{"node":{"html":"<p>A submission from a hacker that describes a potential security vulnerability.</p>","frontmatter":{"path":"/glossary/report","title":"Report"}}},{"node":{"html":"<p>Reputation measures how likely a hacker’s finding is to be immediately relevant and actionable. </p>","frontmatter":{"path":"/glossary/reputation","title":"Reputation"}}},{"node":{"html":"<p>A collection of assets that hackers are to hack on. It’s the structured data that represents the attack surface that’s included or explicitly excluded in an organization’s vulnerability disclosure or bug bounty program. </p>","frontmatter":{"path":"/glossary/scope","title":"Scope"}}},{"node":{"html":"<p>Average <a href=\"/hackers/reputation.html\">reputation</a> gained per report.</p>","frontmatter":{"path":"/glossary/signal","title":"Signal"}}},{"node":{"html":"<p>Weakness of software, hardware, or online service that can be exploited.</p>","frontmatter":{"path":"/glossary/vulnerability","title":"Vulnerability"}}},{"node":{"html":"<p>The process by which an organization receives and disseminates information about vulnerabilities in their products or online services. </p>\n<p>ISO 29147 definition: Process through which vendors and vulnerability finders may work cooperatively in finding solutions that reduce the risks associated with a vulnerability. It encompasses actions such as reporting, coordinating, and publishing information about a vulnerability and its resolution. </p>","frontmatter":{"path":"/glossary/vulnerability-disclosure","title":"Vulnerability Disclosure"}}}]}},"pathContext":{}}

